import React from "react";
import { Select, Space } from "antd";
import face from "assets/png/img.png";
const handleChange = (value: string) => {
	console.log(`selected ${value}`);
};

export const Selectrum: React.FC = () => (
	<Space wrap>
		<Select
			defaultValue=" "
			style={{ width: 300 }}
			onChange={handleChange}
			options={[
				{ value: "   ", label: '   ' },
				{ value: "   ", label: "   " },
				{ value: " ", label: " " },
			]}
		/>
	</Space>
);

export const Selectrumtwo: React.FC = () => (
	<Space wrap>
		<Select
			defaultValue=""
			style={{ width: 150 }}
			onChange={handleChange}
			options={[
				{ value: "", label: '' },
				{ value: "   ", label: "   " },
				{ value: " ", label: " " },
			]}
		/>
	</Space>
);


